import React, {Component} from "react";
import {connect} from "react-redux";
import DataLayerUtils from "eshop/utils/DataLayerUtils";
import {
    updateComparatorDevicesList,
    fetchComparatorConfig
} from "eshop/modules/simfree/actions/comparator";
import {
    getComparatorConfig,
    getComparatorDevices,
    getSelectedCategory
} from "eshop/modules/simfree/selectors";
import {
    getFiltersUrl
} from "eshop/modules/configurator/selectors/filters";
import ActionType from "eshop/modules/simfree/fetchComparatorConfigActionType";
import {getPagePrefixUrl} from "../../../configurator/utils";
import {getMarketContextPrefixUrl} from "../../../configurator/selectors/metaData";

class ComparatorDeviceTab extends Component {

    constructor(props) {
        super(props);
    }

    handleDeleteClick(uid){
        this.props.updateComparatorConfig(uid, this.props.comparatorUid);
    }

    removePrefix(url){
        if(url){
            return url.split("/sklep").pop().split("/akcesoria").pop();
        } else {
            return url;
        }
    }

    getProductUrl(){
        DataLayerUtils.pushComparatorDeviceDetails(this.props.device, this.props.selectedDevices);
        location.href =
            getPagePrefixUrl(this.props.marketContextPrefixUrl) +
            this.removePrefix(this.props.device.productPageUrl) +
            '?' +
            this.props.filtersUrl +
            '&selectedCategory=' +
            this.props.selectedCategory;
    }

    render() {
        return (
            <section className="opl-compare-products__section--box">
                <div key={this.props.device.productId + this.props.index} className="opl-compare-products__section--box-item">
                    <button className="g-icon g-icon--only g-icon--xs g-icon--close clear-content" onClick={() => this.handleDeleteClick(this.props.device.productId)}><span
                        className="u-acc-hide">Usu≈Ñ</span></button>
                    <div
                        className="opl-feature-object opl-feature-object--zoom-in-zoom-out  opl-feature-object--phone u-margin-top">
                        <a onClick={() => this.getProductUrl()} className="opl-feature-object__content">
                            <div className="opl-feature-object__inner-wrapper">
                                <div className="opl-feature-object__inner"><img
                                    src={this.props.device.imageUrl} alt={this.props.device.productId+"_alt"}/></div>
                            </div>
                        </a>
                    </div>
                    <p className="u-font-bold u-text-center">{this.props.device.name}</p>
                </div>
            </section>
        )
    }

}


const mapStateToProps = state => ({
    selectedDevices: getComparatorDevices(state),
    comparatorConfig: getComparatorConfig(state),
    selectedCategory: getSelectedCategory(state),
    filtersUrl: getFiltersUrl(state),
    marketContextPrefixUrl:getMarketContextPrefixUrl(state)
});

const mapDispatchToProps = (dispatch) => ({
    updateSelectedDevices: (deviceUid) => dispatch(updateComparatorDevicesList(deviceUid)),
    updateComparatorConfig: (devices, pk) => dispatch(fetchComparatorConfig(devices, pk, ActionType.REMOVE_DEVICE))
});

export default connect(mapStateToProps, mapDispatchToProps)(ComparatorDeviceTab);
